/* src/components/AnimalCard.css */

.animal-card {
  background: rgba(255, 255, 255, 0.9);
  border-radius: 22px;
  overflow: hidden;
  width: 100%;
  max-width: 330px;
  cursor: pointer;
  transition: all 0.35s ease;
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.animal-card:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 12px 35px rgba(0, 0, 0, 0.25);
}

.animal-card img {
  width: 100%;
  height: 230px;
  object-fit: cover;
  transition: transform 0.4s ease;
}

.animal-card:hover img {
  transform: scale(1.06);
}

.animal-card-content {
  padding: 20px 22px;
  text-align: left;
}

.animal-card-content h3 {
  font-size: 1.3rem;
  color: #003049;
  margin-bottom: 6px;
  font-weight: 700;
}

.animal-card-content p {
  margin: 6px 0;
  color: #555;
  font-size: 0.95rem;
}

.animal-card-content .region {
  color: #0077b6;
  font-weight: 600;
  margin-top: 10px;
}
