/* src/components/AnimalModal.css */

.animal-modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.85);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  overflow-y: auto;
  z-index: 1000;
  padding: 40px 20px;
}

.animal-modal-container {
  background: #ffffff;
  border-radius: 20px;
  max-width: 900px;
  width: 100%;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.4);
  position: relative;
  animation: fadeIn 0.4s ease-in-out;
}

.close-button {
  background: none;
  border: none;
  color: #222;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  position: absolute;
  top: 18px;
  left: 18px;
  padding: 8px 12px;
  border-radius: 10px;
  background-color: #f2f2f2;
  transition: all 0.2s ease-in-out;
}

.close-button:hover {
  background-color: #ddd;
}

.animal-modal-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 60px 40px 40px 40px;
  text-align: left;
}

.animal-modal-image {
  width: 100%;
  max-height: 400px;
  object-fit: cover;
  border-radius: 15px;
  margin-bottom: 25px;
}

.animal-modal-details {
  width: 100%;
}

.animal-modal-details h2 {
  font-size: 2rem;
  margin-bottom: 5px;
  color: #222;
}

.scientific-name {
  font-style: italic;
  color: #777;
  margin-bottom: 20px;
}

.animal-info {
  background: #fafafa;
  padding: 15px 20px;
  border-radius: 10px;
  margin-bottom: 25px;
  line-height: 1.6;
}

.animal-info p {
  margin: 6px 0;
}

.animal-description {
  line-height: 1.8;
  font-size: 1.05rem;
  color: #333;
}

.animal-description h3 {
  font-size: 1.4rem;
  margin-bottom: 10px;
  color: #222;
}

@media (max-width: 700px) {
  .animal-modal-container {
    padding: 15px;
  }

  .animal-modal-content {
    padding: 40px 20px;
  }

  .animal-modal-image {
    max-height: 300px;
  }

  .animal-modal-details h2 {
    font-size: 1.6rem;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
